:root {
  --white: #fff;
  --moderate-violet: #7541c8;
  --light-moderate-violet: #ae90df;
  --very-dark-grayish-blue: #48556a;
  --very-dark-blackish-blue: #19212e;
  --light-gray: #cfcfcf;
  --light-grayish-blue: #ecf2f8;
}

.container {
  background-color: var(--light-grayish-blue);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
}

.sr-only {
  clip: rect(0, 0, 0, 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.testimonials {
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: minmax(200px, auto);
  gap: 25px;
  width: 100%;
  max-width: 20rem;
  font-family: Barlow Semi Condensed, sans-serif;
  font-weight: 500;
  line-height: 1.45em;
  display: grid;
}

@media screen and (min-width: 767px) {
  .testimonials {
    grid-template-columns: repeat(4, 1fr);
    max-width: 72rem;
  }
}

@media screen and (min-width: 1023px) {
  .testimonials {
    max-width: 68rem;
  }
}

.testimonials .testimonial__article {
  box-shadow: 0 12px 12px -3px var(--light-gray);
  border-radius: 8px;
  padding: 18px 25px;
}

@media screen and (min-width: 1280px) {
  .testimonials .testimonial__article {
    max-height: 280px;
  }

  .testimonials .testimonial__article--multi-row {
    max-height: 600px;
  }
}

.testimonials .testimonial__article--grid-item-one {
  grid-area: 1 / 1 / auto / span 3;
}

@media screen and (min-width: 767px) {
  .testimonials .testimonial__article--grid-item-one {
    grid-area: 1 / 1 / auto / span 2;
  }
}

.testimonials .testimonial__article--grid-item-two {
  grid-area: 2 / 1 / auto / span 3;
}

@media screen and (min-width: 767px) {
  .testimonials .testimonial__article--grid-item-two {
    grid-area: 1 / 3 / auto / span 1;
  }
}

.testimonials .testimonial__article--grid-item-three {
  grid-area: 3 / 1 / auto / span 3;
}

@media screen and (min-width: 767px) {
  .testimonials .testimonial__article--grid-item-three {
    grid-area: 2 / 1 / auto / span 1;
  }
}

.testimonials .testimonial__article--grid-item-four {
  grid-area: 4 / 1 / auto / span 3;
}

@media screen and (min-width: 767px) {
  .testimonials .testimonial__article--grid-item-four {
    grid-area: 2 / 2 / auto / span 2;
  }
}

.testimonials .testimonial__article--grid-item-five {
  grid-area: 5 / 1 / auto / span 3;
}

@media screen and (min-width: 767px) {
  .testimonials .testimonial__article--grid-item-five {
    grid-area: span 2 / 4 / auto / span 1;
  }
}

.testimonials .testimonial__article--theme-violet {
  color: var(--white);
  background-color: var(--moderate-violet);
  background-image: url("bg-pattern-quotation.97325254.svg");
  background-position: 77% 0;
  background-repeat: no-repeat;
}

.testimonials .testimonial__article--theme-violet .profile__img {
  border: 2px solid var(--light-moderate-violet);
}

.testimonials .testimonial__article--theme-gray {
  color: var(--white);
  background-color: var(--very-dark-grayish-blue);
}

.testimonials .testimonial__article--theme-white {
  color: var(--very-dark-blackish-blue);
  background-color: var(--white);
}

.testimonials .testimonial__article--theme-blackish {
  color: var(--white);
  background-color: var(--very-dark-blackish-blue);
}

.testimonials .testimonial__article--theme-blackish .profile__img {
  border: 2px solid var(--light-moderate-violet);
}

.testimonials .testimonial__article .testimonial__profile {
  flex-direction: row;
  display: flex;
}

.testimonials .testimonial__article .testimonial__profile .profile__graduate {
  opacity: .5;
  margin-top: -3px;
  font-size: .7rem;
}

.testimonials .testimonial__article .testimonial__profile .profile__name {
  font-size: .85rem;
}

.testimonials .testimonial__article .testimonial__profile .profile__img {
  border-radius: 999px;
  align-self: center;
  width: 32px;
  height: 32px;
  margin-right: 18px;
}

.testimonials .testimonial__article .testimonial__headline {
  margin-top: 12px;
  margin-bottom: 15px;
  font-size: 1.25rem;
  line-height: 1.25em;
}

.testimonials .testimonial__article .testimonial__quote {
  opacity: .6;
  padding-bottom: 1rem;
  font-size: .825rem;
  line-height: 1.15rem;
}

